.container {
    max-width: 960px;
  }
  .site-header {
    background-color: rgba(0, 0, 0, .85);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
  .site-header a {
    color: #999;
    -webkit-transition: ease-in-out color .15s;
    -o-transition: ease-in-out color .15s;
    transition: ease-in-out color .15s;
  }
  .site-header a:hover {
    color: #fff;
    text-decoration: none;
  }
  
  .product-device {
    position: absolute;
    right: 10%;
    bottom: -30%;
    width: 300px;
    height: 540px;
    background-color: #333;
    border-radius: 21px;
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
        transform: rotate(30deg);
  }
  
  .first-pic{
    background: url("https://images.unsplash.com/photo-1430232324554-8f4aebd06683?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=ea7a27e70474efc5e3f61e417626fa98&auto=format&fit=crop&w=1&q=60");
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;  
  }
  .second-pic{
    background: url("https://images.unsplash.com/photo-1518425797340-4e87e7c19f31?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=22705436bdac1cd127f4ccb5952d290c&auto=format&fit=crop&w=1351&q=80");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;  
    border-radius: 21px 21px 0 0;
    height: 300px;
    width: 80%;

  }
  .third-pic{
    background: url("https://images.unsplash.com/photo-1527871369852-eb58cb2b54e2?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=5e2e94705996b10309bc88a058d97cfe&auto=format&fit=crop&w=540&q=80");
    background-position: center ;
    background-repeat: no-repeat;
    background-size: cover;  
    border-radius: 21px 21px 0 0;
    height: 300px;
    width: 80%;
  }
  
  .border-top { border-top: 1px solid #e5e5e5; }
  .border-bottom { border-bottom: 1px solid #e5e5e5; }
  
  .box-shadow { -webkit-box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); }
  
  .flex-equal > * {
    -ms-flex: 1;
    flex: 1 1;
  }
  @media (min-width: 768px) {
    .flex-md-equal > * {
      -ms-flex: 1;
      flex: 1 1;
    }
  }
  
  .overflow-hidden { overflow: hidden; }


